.container {
  margin: 20px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 5px;
}

.card {
  display: grid;
  border: 2px solid black;
  border-radius: 20px;
}

.img {
  border: 1px solid black;
  border-radius: 50%;
  margin: 3px;
  position: relative;
}

.name {
  position: relative;
  position: bottom;
  text-align: center;
}
